#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 2.5rem;
    font-weight: 600;
    padding-top: 3rem;
    margin: 1rem;
}

.skillImages{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    flex-wrap: wrap;
    max-width: 65rem;
}
.skillImg{
    object-fit: cover;
    height: 15rem;
    margin: 0.5rem;
    width: 17rem;
    cursor: pointer;
}
.Img{
    width: 100%;
    height: 100%;
    border-radius: 2rem;
}
.skillText{
    object-fit: cover;
    height: 15rem;
    margin: 0.5rem;
    padding: 1rem;
    width: 17rem;
    background-color: rgb(255, 255, 255);
    color: rgb(100,100,100);
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
}
.skillText p{
    margin: 1rem auto;
}
.skillText div{
    display: flex;
    flex-direction: column;
}
.skillText h3{
    margin: 2rem auto;
}